export default "<template>\r\n  <div :style=\"{ opacity: value / 300 }\">\r\n    <!-- Some heavy duty component -->\r\n    <div>{{ heavy() }}</div>\r\n  </div>\r\n</template>\r\n\r\n<script>\r\nexport default {\r\n  props: ['value'],\r\n  methods: {\r\n    // NOTE: heavy logic\r\n    heavy () {\r\n      const n = 100000\r\n      let result = 0\r\n      for (let i = 0; i < n; i++) {\r\n        result += Math.sqrt(Math.cos(Math.sin(42)))\r\n      }\r\n      return result\r\n    },\r\n  },\r\n}\r\n</script>\r\n";