export default "<template>\r\n  <div :style=\"{ opacity: value / 300 }\">\r\n    <!-- Now we split the component -->\r\n    <ChildComponent/>\r\n  </div>\r\n</template>\r\n\r\n<script>\r\nexport default {\r\n  components: {\r\n    // NOTE: This is the child\r\n    ChildComponent: {\r\n      methods: {\r\n        heavy () {\r\n          const n = 100000\r\n          let result = 0\r\n          for (let i = 0; i < n; i++) {\r\n            result += Math.sqrt(Math.cos(Math.sin(42)))\r\n          }\r\n          return result\r\n        },\r\n      },\r\n      render (h) {\r\n        return h('div', this.heavy())\r\n      },\r\n    },\r\n  },\r\n  props: ['value'],\r\n}\r\n</script>\r\n";